import { useNavigate, useParams } from 'react-router-dom';
import { AppRoot, Title, Text, Button } from '@telegram-apps/telegram-ui';
import { useEffect, useLayoutEffect, useState } from 'react';
import { assets } from './data/assets';
import {
  getDebugInfo,
  ensureWalletConnected,
  getConnectedWallet,
  disconnectWallet,
  subscribeToWalletChanges,
  getTonConnect,
  getJettonWalletAddress,
} from './tonConnectInstance';
import { Address, beginCell, toNano } from '@ton/core';
import { nFormatter } from './components/nFormatter';
import { getJettonsFromTonAPI } from './components/getJettonsFromTonAPI';
import { tonClient } from './tonConnectInstance';

const SHOW_DEBUG = false;
const SHOW_ADMIN = false;

export const AssetBuy = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const asset = assets.find((a) => a.id === id);

  // Скролл вверх при монтировании компонента
  useLayoutEffect(() => {
    document.documentElement.style.scrollBehavior = 'smooth';
    window.scrollTo(0, 0);
    return () => {
      document.documentElement.style.scrollBehavior = 'auto';
    };
  }, []);

  const minInvest = parseInt((asset?.minInvest ?? '0').replace(/\D/g, ''), 10);
  const [amount, setAmount] = useState(minInvest || 0);
  const [balance, setBalance] = useState<number | null>(null);
  const [walletAddress, setWalletAddress] = useState<string | null>(null);
  const [jettonMasterInput, setJettonMasterInput] = useState<string>('');
  const [distributeAmount, setDistributeAmount] = useState<string>('');
  const [adminAddress, setAdminAddress] = useState<string>('');
  const [adminAmount, setAdminAmount] = useState<string>('');

  // Подписка на изменения кошелька
  useEffect(() => {
    const updateWallet = () => {
      try {
        const wallet = getConnectedWallet();
        setWalletAddress(wallet.address);
      } catch {
        setWalletAddress(null);
      }
    };

    updateWallet();
    subscribeToWalletChanges(updateWallet);
  }, []);

  // Получение баланса при изменении кошелька
  useEffect(() => {
    if (!walletAddress) return;

    const fetchBalance = async () => {
      try {
        console.log('Fetching USDT balance for wallet:', walletAddress);
        const jettons = await getJettonsFromTonAPI(walletAddress, ['USDT']);
        console.log('Received jettons data:', jettons);
        const usdtBalance = jettons.find(j => j.symbol === 'USDT')?.balance ?? 0;
        console.log('USDT balance found:', usdtBalance);
        setBalance(usdtBalance);
      } catch (e) {
        console.error('Ошибка при получении баланса USDT:', e);
        setBalance(null);
      }
    };

    fetchBalance();
  }, [walletAddress]);

  if (!asset) {
    return (
      <AppRoot>
        <div style={{ padding: 16 }}>
          <Text>Актив не найден</Text>
          <Button onClick={() => navigate(-1)}>← Назад</Button>
        </div>
      </AppRoot>
    );
  }

  const progress = Math.max(1, Math.floor(asset.raised / asset.goal * 100));
  const remaining = asset.goal - asset.raised;

  const quickButtons = [
    minInvest,
    minInvest * 2,
    Math.floor(remaining / 2 / minInvest) * minInvest,
    Math.floor(remaining / minInvest) * minInvest,
  ].filter((value, index, arr) => arr.indexOf(value) === index && value <= remaining);

  const handleBuy = async () => {
    try {
      await ensureWalletConnected();
      const wallet = getConnectedWallet();

      const jettons = await getJettonsFromTonAPI(wallet.address, ['USDT']);
      const usdtBalance = jettons.find(j => j.symbol === 'USDT')?.balance ?? 0;

      if (usdtBalance < amount) {
        alert(`Недостаточно USDT. Доступно: ${usdtBalance.toFixed(2)} USDT`);
        return;
      }

      if (!asset?.id || !wallet.address) {
        throw new Error('Missing required addresses');
      }

      try {
        console.log('Sending USDT transfer...');
        console.log('From:', wallet.address);
        console.log('Amount:', amount);
        console.log('Destination:', asset.id);
        console.log('Amount in jetton units:', BigInt(amount) * BigInt(1000000));

        // Проверяем адрес USDT кошелька
        const usdtWallet = jettons.find(j => j.symbol === 'USDT');
        if (!usdtWallet) {
          throw new Error('USDT wallet not found');
        }
        console.log('USDT wallet details:', usdtWallet);

        const tonConnect = getTonConnect();
        console.log('TonConnect initialized:', tonConnect);

        // Получаем адрес USDT кошелька отправителя
        const senderUsdtWallet = await getJettonWalletAddress(
          tonClient,
          wallet.address,
          usdtWallet.master
        );
        console.log('Sender USDT wallet address:', senderUsdtWallet);

        // Создаем сообщение для перевода Jetton
        const jettonAmount = BigInt(amount) * BigInt(1000000); // 1 USDT = 1000000 jetton units (6 decimals)
        console.log('Amount in jetton units:', jettonAmount.toString());

        // Создаем сообщение для перевода Jetton по стандарту TIP-3/TEP-74
        const queryId = BigInt(Math.floor(Date.now() / 1000)); // Используем timestamp как query_id
        console.log('Generated query_id:', queryId.toString());
        
        const body = beginCell()
          .storeUint(0xf8a7ea5, 32) // op-код transfer Jetton
          .storeUint(queryId, 64) // query_id на основе timestamp
          .storeCoins(jettonAmount) // количество токенов в минимальных единицах
          .storeAddress(Address.parse(asset.id)) // адрес получателя
          .storeAddress(Address.parse(wallet.address)) // адрес для возврата излишков
          .storeUint(0, 1) // custom_payload отсутствует (0 = None)
          .storeCoins(toNano('0.3')) // forward_ton_amount = 1 нанотон
          .storeUint(0, 1) // forward_payload отсутствует (0 = None)
          .endCell();

        console.log('Message body (base64):', body.toBoc().toString('base64'));
        console.log('Message body (hex):', body.toBoc().toString('hex'));

        const transaction = {
          validUntil: Math.floor(Date.now() / 1000) + 600,
          messages: [{
            address: senderUsdtWallet, // адрес USDT кошелька отправителя
            amount: toNano('0.5').toString(), // 0.067306608 TON для газа
            payload: body.toBoc().toString('base64')
          }]
        };

        console.log('Transaction details:', {
          validUntil: transaction.validUntil,
          message: {
            address: transaction.messages[0].address,
            amount: transaction.messages[0].amount,
            payloadLength: transaction.messages[0].payload.length,
            payload: transaction.messages[0].payload
          }
        });

        try {
          console.log('Attempting to send transaction...');
          const result = await tonConnect.sendTransaction(transaction);
          console.log('Raw transaction result:', result);
          
          if (result && typeof result === 'object') {
            console.log('Transaction details:', {
              boc: result.boc
            });
            
            if (result.boc) {
              alert(`Транзакция успешно отправлена!\nОтправлено: ${amount} USDT`);
            } else {
              throw new Error('Transaction failed - no boc');
            }
          } else {
            throw new Error('Invalid transaction result');
          }
        } catch (txError) {
          console.error('Transaction error:', txError);
          if (txError instanceof Error) {
            alert(`Ошибка при отправке транзакции: ${txError.message}\nПроверьте консоль для подробностей.`);
          } else {
            alert('Неизвестная ошибка при отправке транзакции. Проверьте консоль для подробностей.');
          }
          throw txError;
        }
      } catch (error) {
        console.error('Error sending transfer:', error);
        if (error instanceof Error) {
          alert(`Ошибка при отправке USDT: ${error.message}\nПроверьте консоль для подробностей.`);
        } else {
          alert('Неизвестная ошибка при отправке USDT. Проверьте консоль для подробностей.');
        }
        return;
      }
    } catch (err) {
      console.error(err);
      alert(`Ошибка при отправке USDT - ${err}`);
    }
  };

  const debug = getDebugInfo();

  return (
    <AppRoot>
      <div
        style={{
          minHeight: '100vh',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'space-between',
          padding: 16,
          backgroundColor: 'var(--tg-theme-secondary-bg-color, #f9f9f9)',
        }}
      >
        <div style={{ maxWidth: 530, width: '100%', margin: '0 auto' }}>
          {walletAddress && SHOW_DEBUG && (
            <div style={{ display: 'flex', flexDirection: 'column', marginBottom: 12 }}>
              <Text
                style={{
                  fontSize: 14,
                  color: 'var(--tg-theme-hint-color, #888)',
                  wordBreak: 'break-word',
                  whiteSpace: 'pre-wrap',
                  marginBottom: 4,
                }}
              >
                Адрес:
                {'\n'}
                {walletAddress}
              </Text>
              <Text
                style={{
                  fontSize: 14,
                  color: 'var(--tg-theme-hint-color, #888)',
                  marginBottom: 4,
                }}
              >
                Баланс: {balance === null ? '...' : balance.toFixed(2)} USDT
              </Text>
              <Text
                style={{
                  fontSize: 14,
                  color: 'var(--tg-theme-hint-color, #888)',
                  marginBottom: 8,
                }}
              >
                Сеть: {debug.network}
              </Text>

              {asset.treasuryAddress && SHOW_ADMIN && (
                <div style={{ marginBottom: 8 }}>

                  <Text
                    style={{
                      fontSize: 16,
                      fontWeight: 'bold',
                      color: 'var(--tg-theme-text-color, #000)',
                      marginTop: 16,
                      marginBottom: 8,
                    }}
                  >
                    ----Treasury----
                  </Text>

                  <Text
                    style={{
                      fontSize: 14,
                      color: 'var(--tg-theme-hint-color, #888)',
                      marginBottom: 4,
                    }}
                  >
                    Адрес Jetton Master контракта:
                  </Text>
                  <input
                    type="text"
                    value={jettonMasterInput}
                    onChange={(e) => setJettonMasterInput(e.target.value)}
                    style={{
                      width: '100%',
                      padding: '8px',
                      marginBottom: 8,
                      borderRadius: 8,
                      border: '1px solid var(--tg-theme-hint-color, #888)',
                      backgroundColor: 'var(--tg-theme-bg-color, #fff)',
                      color: 'var(--tg-theme-text-color, #000)',
                    }}
                  />
                  <Button
                    onClick={async () => {
                      try {
                        if (!asset.treasuryAddress) {
                          console.error('Treasury address is not defined');
                          return;
                        }

                        const treasuryAddr = typeof asset.treasuryAddress === 'string' 
                            ? Address.parse(asset.treasuryAddress)
                            : asset.treasuryAddress;

                        const jettonMasterAddress = Address.parse(jettonMasterInput);

                        const messageCell = beginCell()
                          .storeUint(0x1234567b, 32) // правильный op-code для SetJettonMaster
                          .storeAddress(jettonMasterAddress) // адрес мастера
                          .endCell();

                        const boc = messageCell.toBoc();
                        const base64url = btoa(String.fromCharCode.apply(null, Array.from(new Uint8Array(boc))))
                          .replace(/\+/g, '-')
                          .replace(/\//g, '_')
                          .replace(/=+$/, '');

                        const tonConnect = getTonConnect();
                        try {
                            await tonConnect.sendTransaction({
                                validUntil: Math.floor(Date.now() / 1000) + 600,
                                messages: [{
                                    address: treasuryAddr.toString(),
                                    amount: toNano('0.05').toString(),
                                    payload: base64url
                                }]
                            });
                            console.log('SetJettonMaster transaction sent successfully');
                        } catch (error) {
                            console.error('Error sending SetJettonMaster transaction:', error);
                            throw error;
                        }
                      } catch (error) {
                        console.error('Error sending SetJettonMaster transaction:', error);
                      }
                    }}
                    size="s"
                    style={{
                      backgroundColor: '#eee',
                      color: '#000',
                    }}
                  >
                    Set Jetton Master
                  </Button>

                  <div style={{ marginTop: 16 }}>
                    <Text
                      style={{
                        fontSize: 14,
                        color: 'var(--tg-theme-hint-color, #888)',
                        marginBottom: 4,
                      }}
                    >
                      Адрес USDT кошелька Сокровищницы:
                    </Text>
                    <input
                      type="text" 
                      value={jettonMasterInput}
                      onChange={(e) => setJettonMasterInput(e.target.value)}
                      style={{
                        width: '100%',
                        padding: '8px',
                        marginBottom: 8,
                        borderRadius: 8,
                        border: '1px solid var(--tg-theme-hint-color, #888)',
                        backgroundColor: 'var(--tg-theme-bg-color, #fff)',
                        color: 'var(--tg-theme-text-color, #000)',
                      }}
                    />
                    <Button
                      onClick={async () => {
                        try {
                          if (!asset.treasuryAddress) {
                            console.error('Treasury address is not defined');
                            return;
                          }

                          const treasuryAddr = typeof asset.treasuryAddress === 'string' 
                              ? Address.parse(asset.treasuryAddress)
                              : asset.treasuryAddress;

                          const walletAddress = Address.parse(jettonMasterInput);

                          const messageCell = beginCell()
                            .storeUint(0x1234567a, 32) // op-code для SetJettonWallet
                            .storeAddress(walletAddress) // адрес кошелька
                            .endCell();

                          const boc = messageCell.toBoc();
                          const base64url = btoa(String.fromCharCode.apply(null, Array.from(new Uint8Array(boc))))
                            .replace(/\+/g, '-')
                            .replace(/\//g, '_')
                            .replace(/=+$/, '');

                          const tonConnect = getTonConnect();
                          try {
                              await tonConnect.sendTransaction({
                                  validUntil: Math.floor(Date.now() / 1000) + 600,
                                  messages: [{
                                      address: treasuryAddr.toString(),
                                      amount: toNano('0.05').toString(),
                                      payload: base64url
                                  }]
                              });
                              console.log('SetJettonWallet transaction sent successfully');
                          } catch (error) {
                              console.error('Error sending SetJettonWallet transaction:', error);
                              throw error;
                          }
                        } catch (error) {
                          console.error('Error sending SetJettonWallet transaction:', error);
                        }
                      }}
                      size="s"
                      style={{
                        backgroundColor: '#eee',
                        color: '#000',
                      }}
                    >
                      Set USDT Wallet
                    </Button>
                  </div>

                  <div style={{ marginTop: 16 }}>
                    <Text
                      style={{
                        fontSize: 14,
                        color: 'var(--tg-theme-hint-color, #888)',
                        marginBottom: 4,
                      }}
                    >
                      Количество USDT для распределения:
                    </Text>
                    <input
                      type="number"
                      value={distributeAmount}
                      onChange={(e) => setDistributeAmount(e.target.value)}
                      style={{
                        width: '100%',
                        padding: '8px',
                        marginBottom: 8,
                        borderRadius: 8,
                        border: '1px solid var(--tg-theme-hint-color, #888)',
                        backgroundColor: 'var(--tg-theme-bg-color, #fff)',
                        color: 'var(--tg-theme-text-color, #000)',
                      }}
                    />
                    <Button
                      onClick={async () => {
                        try {
                          if (!asset.treasuryAddress) {
                            console.error('Treasury address is not defined');
                            return;
                          }

                          const treasuryAddr = typeof asset.treasuryAddress === 'string' 
                              ? Address.parse(asset.treasuryAddress)
                              : asset.treasuryAddress;

                          const amount = BigInt(parseFloat(distributeAmount) * 1000000); // Умножаем на 6 знаков

                          const messageCell = beginCell()
                            .storeUint(0x12345678, 32) // op-code для DistributeUSDT
                            .storeCoins(amount) // количество USDT в минимальных единицах
                            .endCell();

                          const boc = messageCell.toBoc();
                          const base64url = btoa(String.fromCharCode.apply(null, Array.from(new Uint8Array(boc))))
                            .replace(/\+/g, '-')
                            .replace(/\//g, '_')
                            .replace(/=+$/, '');

                          const tonConnect = getTonConnect();
                          try {
                              await tonConnect.sendTransaction({
                                  validUntil: Math.floor(Date.now() / 1000) + 600,
                                  messages: [{
                                      address: treasuryAddr.toString(),
                                      amount: toNano('0.2').toString(),
                                      payload: base64url
                                  }]
                              });
                              console.log('DistributeUSDT transaction sent successfully');
                          } catch (error) {
                              console.error('Error sending DistributeUSDT transaction:', error);
                              throw error;
                          }
                        } catch (error) {
                          console.error('Error sending DistributeUSDT transaction:', error);
                        }
                      }}
                      size="s"
                      style={{
                        backgroundColor: '#eee',
                        color: '#000',
                      }}
                    >
                      Distribute USDT
                    </Button>
                  </div>

                  <Text
                    style={{
                      fontSize: 16,
                      fontWeight: 'bold',
                      color: 'var(--tg-theme-text-color, #000)',
                      marginTop: 16,
                      marginBottom: 8,
                    }}
                  >
                    ----JettonMaster----
                  </Text>

                  <div style={{ marginBottom: 8 }}>
                    <Text
                      style={{
                        fontSize: 14,
                        color: 'var(--tg-theme-hint-color, #888)',
                        marginBottom: 4,
                      }}
                    >
                      Адрес Treasury:
                    </Text>
                    <input
                      type="text"
                      value={jettonMasterInput}
                      onChange={(e) => setJettonMasterInput(e.target.value)}
                      style={{
                        width: '100%',
                        padding: '8px',
                        marginBottom: 8,
                        borderRadius: 8,
                        border: '1px solid var(--tg-theme-hint-color, #888)',
                        backgroundColor: 'var(--tg-theme-bg-color, #fff)',
                        color: 'var(--tg-theme-text-color, #000)',
                      }}
                    />
                    <Button
                      onClick={async () => {
                        try {
                          if (!asset.id) {
                            console.error('Jetton Master address is not defined');
                            return;
                          }

                          const jettonMasterAddr = Address.parse(asset.id);
                          const treasuryAddress = Address.parse(jettonMasterInput);

                          const messageCell = beginCell()
                            .storeUint(0x1234568e, 32) // op-code для SetTreasuryAddress
                            .storeAddress(treasuryAddress) // адрес казначейства
                            .endCell();

                          const boc = messageCell.toBoc();
                          const base64url = btoa(String.fromCharCode.apply(null, Array.from(new Uint8Array(boc))))
                            .replace(/\+/g, '-')
                            .replace(/\//g, '_')
                            .replace(/=+$/, '');

                          const tonConnect = getTonConnect();
                          try {
                              await tonConnect.sendTransaction({
                                  validUntil: Math.floor(Date.now() / 1000) + 600,
                                  messages: [{
                                      address: jettonMasterAddr.toString(),
                                      amount: toNano('0.05').toString(),
                                      payload: base64url
                                  }]
                              });
                              console.log('SetTreasuryAddress transaction sent successfully');
                          } catch (error) {
                              console.error('Error sending SetTreasuryAddress transaction:', error);
                              throw error;
                          }
                        } catch (error) {
                          console.error('Error sending SetTreasuryAddress transaction:', error);
                        }
                      }}
                      size="s"
                      style={{
                        backgroundColor: '#eee',
                        color: '#000',
                      }}
                    >
                      Set Treasury Address
                    </Button>
                  </div>


                  <div style={{ marginTop: 16 }}>
                    <Text
                      style={{
                        fontSize: 14,
                        color: 'var(--tg-theme-hint-color, #888)',
                        marginBottom: 4,
                      }}
                    >
                      Адрес для mint/burn:
                    </Text>
                    <input
                      type="text"
                      value={adminAddress}
                      onChange={(e) => setAdminAddress(e.target.value)}
                      style={{
                        width: '100%',
                        padding: '8px',
                        marginBottom: 8,
                        borderRadius: 8,
                        border: '1px solid var(--tg-theme-hint-color, #888)',
                        backgroundColor: 'var(--tg-theme-bg-color, #fff)',
                        color: 'var(--tg-theme-text-color, #000)',
                      }}
                    />
                    <Text
                      style={{
                        fontSize: 14,
                        color: 'var(--tg-theme-hint-color, #888)',
                        marginBottom: 4,
                      }}
                    >
                      Количество USDT:
                    </Text>
                    <input
                      type="number"
                      value={adminAmount}
                      onChange={(e) => setAdminAmount(e.target.value)}
                      style={{
                        width: '100%',
                        padding: '8px',
                        marginBottom: 8,
                        borderRadius: 8,
                        border: '1px solid var(--tg-theme-hint-color, #888)',
                        backgroundColor: 'var(--tg-theme-bg-color, #fff)',
                        color: 'var(--tg-theme-text-color, #000)',
                      }}
                    />
                    <div style={{ display: 'flex', gap: 8 }}>
                      <Button
                        onClick={async () => {
                          try {
                            if (!asset.id) {
                              console.error('Jetton Master address is not defined');
                              return;
                            }

                            const jettonMasterAddr = Address.parse(asset.id);
                            const toAddr = Address.parse(adminAddress);
                            const amount = BigInt(parseFloat(adminAmount) * 1000000); // Умножаем на 6 знаков

                            const messageCell = beginCell()
                              .storeUint(0x1234568b, 32) // op-code для AdminMint
                              .storeAddress(toAddr) // адрес получателя
                              .storeCoins(amount) // количество USDT в минимальных единицах
                              .endCell();

                            const boc = messageCell.toBoc();
                            const base64url = btoa(String.fromCharCode.apply(null, Array.from(new Uint8Array(boc))))
                              .replace(/\+/g, '-')
                              .replace(/\//g, '_')
                              .replace(/=+$/, '');

                            const tonConnect = getTonConnect();
                            try {
                                await tonConnect.sendTransaction({
                                    validUntil: Math.floor(Date.now() / 1000) + 600,
                                    messages: [{
                                        address: jettonMasterAddr.toString(),
                                        amount: toNano('0.4').toString(),
                                        payload: base64url
                                    }]
                                });
                                console.log('AdminMint transaction sent successfully');
                            } catch (error) {
                                console.error('Error sending AdminMint transaction:', error);
                                throw error;
                            }
                          } catch (error) {
                            console.error('Error sending AdminMint transaction:', error);
                          }
                        }}
                        size="s"
                        style={{
                          flex: 1,
                          backgroundColor: '#eee',
                          color: '#000',
                        }}
                      >
                        Admin Mint
                      </Button>
                      <Button
                        onClick={async () => {
                          try {
                            if (!asset.id) {
                              console.error('Jetton Master address is not defined');
                              return;
                            }

                            const jettonMasterAddr = Address.parse(asset.id);
                            const fromAddr = Address.parse(adminAddress);
                            const amount = BigInt(parseFloat(adminAmount) * 1000000); // Умножаем на 6 знаков

                            const messageCell = beginCell()
                              .storeUint(0x1234568c, 32) // op-code для AdminBurn
                              .storeAddress(fromAddr) // адрес отправителя
                              .storeCoins(amount) // количество USDT в минимальных единицах
                              .endCell();

                            const boc = messageCell.toBoc();
                            const base64url = btoa(String.fromCharCode.apply(null, Array.from(new Uint8Array(boc))))
                              .replace(/\+/g, '-')
                              .replace(/\//g, '_')
                              .replace(/=+$/, '');

                            const tonConnect = getTonConnect();
                            try {
                                await tonConnect.sendTransaction({
                                    validUntil: Math.floor(Date.now() / 1000) + 600,
                                    messages: [{
                                        address: jettonMasterAddr.toString(),
                                        amount: toNano('0.4').toString(),
                                        payload: base64url
                                    }]
                                });
                                console.log('AdminBurn transaction sent successfully');
                            } catch (error) {
                                console.error('Error sending AdminBurn transaction:', error);
                                throw error;
                            }
                          } catch (error) {
                            console.error('Error sending AdminBurn transaction:', error);
                          }
                        }}
                        size="s"
                        style={{
                          flex: 1,
                          backgroundColor: '#eee',
                          color: '#000',
                        }}
                      >
                        Admin Burn
                      </Button>
                    </div>
                  </div>
                </div>
              )}


              <Button
                onClick={disconnectWallet}
                size="s"
                style={{
                  alignSelf: 'flex-start',
                  backgroundColor: '#eee',
                  color: '#000',
                  marginBottom: 8,
                }}
              >
                Отключить кошелёк
              </Button>
            </div>
          )}

          <Button
            onClick={() => navigate(-1)}
            size="l"
            style={{
              marginBottom: 16,
              backgroundColor: 'var(--tg-theme-bg-color, #eee)',
              color: 'var(--tg-theme-text-color, #000)',
            }}
          >
            ← Назад
          </Button>

          <div
            style={{
              padding: 16,
              backgroundColor: 'var(--tg-theme-bg-color, #fff)',
              borderRadius: 16,
              marginBottom: 24,
            }}
          >
            <Title level="2" style={{ marginBottom: 4, color: 'var(--tg-theme-text-color, #000)' }}>
              {asset.name}
            </Title>
            <Text style={{ color: 'var(--tg-theme-hint-color, #666)', marginBottom: 8 }}>
              От {asset.minInvest} USDT • Доходность {asset.returnRate}
            </Text>

            <div
              style={{
                height: 6,
                backgroundColor: 'var(--tg-theme-secondary-bg-color, #eee)',
                borderRadius: 4,
                overflow: 'hidden',
                marginBottom: 8,
              }}
            >
              <div
                style={{
                  height: '100%',
                  width: `${progress}%`,
                  backgroundColor: 'var(--tg-theme-link-color, #3390ec)',
                }}
              />
            </div>

            <Text style={{ fontSize: 14, color: 'var(--tg-theme-text-color, #000)' }}>
              {asset.raised.toLocaleString()} USDT ({progress}%) из{' '}
              {asset.goal.toLocaleString()} USDT
            </Text>

            <div
  style={{
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    backgroundColor: 'var(--tg-theme-bg-color, #fff)',
    borderRadius: 12,
    padding: '12px 16px',
    marginBottom: 16,
    border: '1px solid #ccc',
  }}
>
  <Button
    size="s"
    style={{
      backgroundColor: '#fdd',
      color: '#a00',
      minWidth: 40,
      height: 40,
    }}
    onClick={() => {
      const newAmount = Math.max(minInvest, amount - minInvest);
      setAmount(newAmount);
    }}
  >
    {'- ' + nFormatter(minInvest, 1)}
  </Button>

  <Text style={{ fontSize: 18, fontWeight: 500, color: 'var(--tg-theme-text-color, #000)', }}>
    {amount.toLocaleString()} USDT
  </Text>

  <Button
    size="s"
    style={{
      backgroundColor: '#dfd',
      color: '#070',
      minWidth: 40,
      height: 40,
    }}
    onClick={() => {
      const newAmount = Math.min(remaining, amount + minInvest);
      setAmount(newAmount);
    }}
  >
    {'+ ' + nFormatter(minInvest, 1)}
  </Button>
</div>

            <div
              style={{
                display: 'flex',
                flexWrap: 'wrap',
                gap: 8,
                justifyContent: 'space-between',
                marginBottom: 16,
              }}
            >
              {quickButtons.map((value) => (
                <Button
                  key={value}
                  style={{
                    flex: '1 1 45%',
                    backgroundColor: 'var(--tg-theme-secondary-bg-color, #eee)',
                    color: 'var(--tg-theme-text-color, #000)',
                  }}
                  onClick={() => setAmount(value)}
                >
                  {nFormatter(value, 1)}
                </Button>
              ))}
            </div>
          </div>
        </div>

        <div style={{ maxWidth: 530, width: '100%', margin: '0 auto' }}>
          <Button
            size="l"
            style={{ width: '100%', marginTop: 16, marginBottom: 24+100 }}
            onClick={() => {
              window.scrollTo(0, 0);
              handleBuy();
            }}
          >
            Купить на {amount.toLocaleString()} USDT
          </Button>
        </div>
      </div>
    </AppRoot>
  );
};
