// src/tonConnectInstance.tsx
import { TonConnectUI } from '@tonconnect/ui';
import { TonClient } from '@ton/ton';
import { getJettonTransferPayload, toNano, getJettonWalletAddress, getJettonBalance } from './jetton-utils';

declare global {
  interface Window {
    tonConnectUI?: TonConnectUI;
  }
}

const manifestUrl = 'https://merc1305.github.io/build/tonconnect-manifest.json';
export const TON_ENDPOINT = 'https://testnet.toncenter.com/api/v2/jsonRPC';

export const tonClient = new TonClient({ endpoint: TON_ENDPOINT });

export function getTonConnect(): TonConnectUI {
  if (typeof window === 'undefined') {
    throw new Error('TonConnect должен вызываться в браузере');
  }

  if (!window.tonConnectUI) {
    window.tonConnectUI = new TonConnectUI({
      manifestUrl,
      restoreConnection: true,
    });
  }

  return window.tonConnectUI;
}

export async function ensureWalletConnected() {
  const tonConnect = getTonConnect();
  if (!tonConnect.account) {
    await tonConnect.connectWallet();
  }
}

export function getConnectedWallet() {
  const tonConnect = getTonConnect();
  const wallet = tonConnect.account;
  if (!wallet) throw new Error('Кошелёк не подключён');
  return wallet;
}

export async function sendJettonTransfer(toAddress: string, amountNano: string) {
  const tonConnect = getTonConnect();
  const wallet = getConnectedWallet();

  const tx = {
    validUntil: Math.floor(Date.now() / 1000) + 60,
    messages: [
      {
        address: wallet.address,
        amount: '0',
        payload: getJettonTransferPayload(toAddress, amountNano),
      },
    ],
  };

  await tonConnect.sendTransaction(tx);
}

export async function disconnectWallet() {
  const tonConnect = getTonConnect();
  await tonConnect.disconnect();
}

export function subscribeToWalletChanges(callback: () => void) {
  const tonConnect = getTonConnect();
  tonConnect.onStatusChange(callback);
}

export function getNetworkName(): string {
  return TON_ENDPOINT.includes('testnet') ? 'testnet' : 'mainnet';
}

export function getDebugInfo(): { address: string | null; network: string } {
  const tonConnect = getTonConnect();
  const wallet = tonConnect.account;

  return {
    address: wallet?.address ?? null,
    network: getNetworkName(),
  };
}

// Переэкспорт утилит
export {
  toNano,
  getJettonWalletAddress,
  getJettonBalance,
};
