import { Address, beginCell, ContractProvider } from '@ton/core';
import { TonClient } from '@ton/ton';

export const toNano = (amount: number) => BigInt(Math.floor(amount * 1e9)).toString();

export const getJettonTransferPayload = (recipient: string, amountNano: string) => {
  const OP_TRANSFER = 0xf8a7ea5;

  const payload = [
    OP_TRANSFER.toString(16).padStart(8, '0'),
    '0000000000000000',
    BigInt(amountNano).toString(16).padStart(32, '0'),
    Address.parse(recipient).toString({ bounceable: true }).replace('0:', '').padStart(64, '0'),
    '0000000000000000',
    '00',
    '00000000',
    '00',
  ].join('');

  return `0x${payload}`;
};

// Получить адрес Jetton Wallet через метод get_wallet_address у Jetton Master
export const getJettonWalletAddress = async (
  client: TonClient,
  userAddress: string,
  jettonMasterAddress: string
): Promise<string> => {
  const master = client.provider(Address.parse(jettonMasterAddress));
  const addressCell = beginCell().storeAddress(Address.parse(userAddress)).endCell();

  const result = await master.get('get_wallet_address', [
    { type: 'slice', cell: addressCell },
  ]);

  const walletAddress = result.stack.readAddress();
  return walletAddress.toString();
};

// Безопасный метод получения баланса Jetton Wallet (возвращает 0 при ошибке)
export const getJettonBalance = async (
  provider: ContractProvider
): Promise<bigint> => {
  try {
    const result = await provider.get('get_wallet_data', []);
    return result.stack.readBigNumber();
  } catch (err: any) {
    console.warn('Jetton Wallet неактивен или не инициализирован:', err.message);
    return BigInt(0);
  }
};
