import { useEffect, useState } from 'react';
import { AppRoot, Button } from '@telegram-apps/telegram-ui';
import { getTonConnect } from './tonConnectInstance';
import { ProfileRowContainer } from './components/ProfileRowContainer';
import { ProfileRow } from './components/ProfileRow';
import { getJettonsFromTonAPI } from './components/getJettonsFromTonAPI';
import md5 from 'md5';

type MyUser = {
  first_name: string;
  last_name?:  string;
  username?:   string;
  photo_url?:  string;
};

const defaultUsername   = 'johndoe';
const defaultFirstName  = 'John';
const defaultLastName   = 'Doe';

export const Projects = () => {
  const rawUser = typeof window !== 'undefined' && window.Telegram?.WebApp?.initDataUnsafe?.user;
  const user = (rawUser as unknown as MyUser) || null;
  const username = user?.username; 
  const photoUrl = user?.photo_url;

  const tonConnect = getTonConnect();
  const [account, setAccount] = useState(tonConnect.account);
  const [totalAssets, setTotalAssets] = useState<number>(0);
  const [totalUSDT, setTotalUSDT] = useState<number>(0);

  // USDT, TGRM — любые названия, которые хочешь суммировать
  const tokenSymbols = ['USDT', 'SOS1', 'SOS2']; // передаем сюда USDT для того, что бы посчитать всё одним запросом, дальше вычтем

  // Обработка смены кошелька
  useEffect(() => {
    const unsubscribe = tonConnect.onStatusChange((wallet) => {
      const newAccount = wallet?.account || null;
      setAccount(newAccount);
    });
    return () => unsubscribe();
  }, [tonConnect]);

  useEffect(() => {
    console.log('💰 totalAssets обновлён:', totalAssets);
  }, [totalAssets]);

  // Пересчёт суммы при заходе на страницу или смене кошелька
  useEffect(() => {
    const fetchJettons = async () => {
      if (!account?.address) {
        setTotalAssets(0);
        setTotalUSDT(0);
        return;
      }

      try {
        const jettons = await getJettonsFromTonAPI(account.address, tokenSymbols);

        const usdt = jettons.find((j) => j.symbol === 'USDT');
        const usdtBalance = usdt ? usdt.balance : 0;
        setTotalUSDT(usdtBalance);

        const sum = jettons.reduce((acc, j) => acc + j.balance, 0) - usdtBalance; // Вот тут вычитам USDT из общей суммы жетонов
        setTotalAssets(sum);
      } catch (e) {
        console.error('Ошибка при получении Jetton балансов:', e);
        setTotalAssets(0);
        setTotalUSDT(0);
      }
    };

    fetchJettons();
  }, [account]);

  const handleConnect = () => tonConnect.openModal();
  const handleDisconnect = () => tonConnect.disconnect();

  const firstName = user?.first_name || defaultFirstName;
  const lastName  = user?.last_name  || defaultLastName;
  const nameForHash = `${firstName}${lastName}`.trim().toLowerCase();
  const avatarHash  = md5(nameForHash);
  const avatarUrl   = `https://www.gravatar.com/avatar/${avatarHash}?d=identicon`;

  return (
    <AppRoot>
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          backgroundColor: 'var(--tg-theme-secondary-bg-color, rgb(255, 255, 255))',
          //minHeight: '100vh',
          padding: 16,
          paddingBottom: 50 
        }}
      >
        <div style={{ width: '100%', maxWidth: 530 }}>
          <div style={{ display: 'flex', alignItems: 'center', marginBottom: 16 }}>
            <img
              src={photoUrl || avatarUrl}
              alt="Avatar"
              style={{ width: 200, height: 200, borderRadius: '50%' }}
            />
            <div style={{ marginLeft: 12, lineHeight: 1.2 }}>
              <div style={{ fontSize: 30, color: 'var(--tg-theme-text-color, #000)' }}>
                @{username || defaultUsername}
              </div>
              <div style={{ fontSize: 30, color: 'var(--tg-theme-text-color, #000)' }}>
                {firstName + ' ' + lastName}
              </div>
            </div>
          </div>

          {account?.address && (
  <ProfileRowContainer>
    <ProfileRow label="Адрес:" value={account.address} copyable />
    <ProfileRow
      label="Всего проинвестировано:"
      value={`${totalAssets.toFixed(0)} USDT`}
    />
    <ProfileRow
      label="Доступно USDT:"
      value={`${totalUSDT.toFixed(0)} USDT`}
      last
    />
  </ProfileRowContainer>
)}

          <Button
            size="l"
            style={{ width: '100%', maxWidth: 510, marginBottom: 16 }}
            onClick={account ? handleDisconnect : handleConnect}
          >
            {account ? 'Отвязать кошелёк' : 'Привязать кошелек TON'}
          </Button>
        </div>
      </div>
    </AppRoot>
  );
};
