import { Address } from '@ton/core';

type JettonBalance = {
    symbol: string;
    name: string;
    balance: number;
    master: string;
  };
  
  const knownDecimals: Record<string, number> = {
    USDT: 6,
    CVRINVEST: 6,
    // Добавляй свои токены сюда при необходимости
  };
  
  export async function getJettonsFromTonAPI(
    walletAddress: string,
    symbolsToInclude?: string[]
  ): Promise<JettonBalance[]> {
    // Convert raw address to base64url format
    const formattedAddress = Address.parse(walletAddress).toString({ urlSafe: true, bounceable: false });
    console.log('Formatted address for API:', formattedAddress);
  
    const response = await fetch(`https://testnet.tonapi.io/v2/accounts/${formattedAddress}/jettons`, {
      headers: {
        Accept: 'application/json',
      },
    });
  
    if (!response.ok) {
      throw new Error(`TON API error: ${response.statusText}`);
    }
  
    const data = await response.json();
    console.log('Raw API response:', data);
  
    if (!data?.balances || !Array.isArray(data.balances)) {
      throw new Error('Invalid TON API response');
    }
  
    const result: JettonBalance[] = data.balances.map((entry: any) => {
      const symbol = entry.jetton?.symbol ?? '';
      const name = entry.jetton?.name ?? '';
      const master = entry.jetton?.address ?? '';
      const decimals = knownDecimals[symbol.toUpperCase()] ?? 6;
      const rawBalance = Number(entry.balance);
      const balance = rawBalance / Math.pow(10, decimals);
  
      return { symbol, name, balance, master };
    });
  
    if (symbolsToInclude && symbolsToInclude.length > 0) {
      const lowered = symbolsToInclude.map((s) => s.toLowerCase());
      return result.filter((j) =>
        lowered.some((sym) =>
          j.symbol.toLowerCase() === sym || j.name.toLowerCase().includes(sym)
        )
      );
    }
  
    return result;
  }
  