import { useState } from 'react';
import { Text } from '@telegram-apps/telegram-ui';

type Props = {
  label: string;
  value: string | number;
  hint?: string;
  last?: boolean;
  copyable?: boolean;
};

export const ProfileRow = ({ label, value, hint, last = false, copyable = false }: Props) => {
  const full = String(value);
  const short = full.length > 10 ? `${full.slice(0, 6)}...${full.slice(-4)}` : full;

  const [copied, setCopied] = useState(false);

  const handleCopy = () => {
    navigator.clipboard.writeText(full);
    setCopied(true);
    setTimeout(() => setCopied(false), 1500);
  };

  return (
    <div
      style={{
        padding: '16px 0',
        borderBottom: last ? 'none' : '1px solid var(--tg-theme-secondary-bg-color, #eee)',
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: hint ? 'flex-start' : 'center',
        gap: 16,
      }}
    >
      {/* Левая часть */}
      <div style={{ flex: 1 }}>
        <Text
          style={{
            color: 'var(--tg-theme-text-color, #000)',
            fontWeight: 600,
            fontSize: 16,
            lineHeight: '20px',
          }}
        >
          {label}
        </Text>
        {hint && (
            <div>
          <Text
            style={{
              color: 'var(--tg-theme-hint-color, #888)',
              fontSize: 13,
              lineHeight: '18px',
              marginTop: 4,
            }}
          >
            {hint}
          </Text>
          </div>
        )}
      </div>

      {/* Правая часть */}
      {copyable ? (
        <div
          onClick={handleCopy}
          style={{
            display: 'flex',
            alignItems: 'center',
            gap: 8,
            minWidth: 100,
            cursor: 'pointer',
            color: 'var(--tg-theme-link-color, #3390ec)',
            fontWeight: 600,
            fontSize: 16,
            textDecoration: 'underline',
          }}
        >
          <span>{short}</span>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              width: 20,
              height: 20,
              backgroundColor: 'transparent',
            }}
          >
            {copied ? (
              <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="green" viewBox="0 0 24 24">
                <path d="M20.285 6.709a1 1 0 0 1 0 1.414l-10 10a1 1 0 0 1-1.414 0l-5-5a1 1 0 1 1 1.414-1.414L10 15.586l9.285-9.291a1 1 0 0 1 1.414 0z" />
              </svg>
            ) : (
              <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="#888" viewBox="0 0 16 16">
                <path
                  fill="currentColor"
                  fillRule="evenodd"
                  d="M5.447 2.396c-.272.534-.272 1.234-.272 2.635v.005H5.01c-1.4 0-2.1 0-2.635.272a2.5 2.5 0 0 0-1.093 1.093c-.272.535-.272 1.235-.272 2.635v1c0 1.4 0 2.1.272 2.635a2.5 2.5 0 0 0 1.093 1.093c.535.272 1.235.272 2.635.272h1c1.4 0 2.1 0 2.635-.272a2.5 2.5 0 0 0 1.093-1.093c.272-.535.272-1.235.272-2.635v-.005h.165c1.4 0 2.1 0 2.634-.273a2.5 2.5 0 0 0 1.093-1.092c.273-.535.273-1.235.273-2.635v-1c0-1.4 0-2.1-.273-2.635a2.5 2.5 0 0 0-1.092-1.093c-.535-.272-1.235-.272-2.636-.272h-1c-1.4 0-2.1 0-2.634.272a2.5 2.5 0 0 0-1.093 1.093M10.01 9.03h.765c.84 0 1.26 0 1.58-.164a1.5 1.5 0 0 0 .656-.655c.164-.321.164-.741.164-1.581v-2.2c0-.84 0-1.26-.164-1.581a1.5 1.5 0 0 0-.655-.656c-.321-.163-.741-.163-1.582-.163h-2.2c-.84 0-1.26 0-1.58.163a1.5 1.5 0 0 0-.656.656c-.163.32-.163.74-.163 1.58v.606c1.29 0 1.957.01 2.47.272a2.5 2.5 0 0 1 1.093 1.093c.272.534.272 1.233.272 2.63m-8-.595c0-.84 0-1.26.164-1.58a1.5 1.5 0 0 1 .655-.656c.32-.164.74-.164 1.581-.164h2.2c.84 0 1.26 0 1.581.164a1.5 1.5 0 0 1 .656.655c.163.32.163.741.163 1.581v2.2c0 .84 0 1.26-.163 1.581a1.5 1.5 0 0 1-.656.656c-.32.163-.74.163-1.581.163h-2.2c-.84 0-1.26 0-1.581-.163a1.5 1.5 0 0 1-.655-.656c-.164-.32-.164-.74-.164-1.58z"
                  clipRule="evenodd"
                />
              </svg>
            )}
          </div>
        </div>
      ) : (
        <div
          style={{
            minWidth: 100,
            color: 'var(--tg-theme-text-color, #000)',
            fontWeight: 600,
            fontSize: 16,
          }}
        >
          {value}
        </div>
      )}
    </div>
  );
};
