import { useParams, useNavigate } from 'react-router-dom';
import { AppRoot, Title, Text, Button } from '@telegram-apps/telegram-ui';
import { assets } from './data/assets';
import { ProfileRow } from './components/ProfileRow';
import { ProfileRowContainer } from './components/ProfileRowContainer';
import { Address, beginCell, toNano } from '@ton/core';
import { getTonConnect } from './tonConnectInstance';


export const AssetDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const asset = assets.find((a) => a.id === id);
  const promoDoc = asset?.promoDoc;

  if (!asset) {
    return (
      <AppRoot>
        <div style={{ padding: 16 }}>
          <Text>Актив не найден</Text>
          <Button onClick={() => navigate(-1)}>← Назад</Button>
        </div>
      </AppRoot>
    );
  }

  const rawProgress = asset.raised / asset.goal;
  const progress = asset.raised > 0 ? Math.max(1, Math.floor(rawProgress * 100)) : 0;

  const handleClaim = async () => {
    try {
      if (!asset.treasuryAddress) {
        console.error('Treasury address is not defined');
        return;
      }

      const treasuryAddr = typeof asset.treasuryAddress === 'string' 
          ? Address.parse(asset.treasuryAddress)
          : asset.treasuryAddress;

      const messageCell = beginCell()
        .storeUint(0x1234567c, 32) // op-code для ClaimJetton
        .endCell();

      const boc = messageCell.toBoc();
      const base64url = btoa(String.fromCharCode.apply(null, Array.from(new Uint8Array(boc))))
        .replace(/\+/g, '-')
        .replace(/\//g, '_')
        .replace(/=+$/, '');

      const tonConnect = getTonConnect();
      try {
          await tonConnect.sendTransaction({
              validUntil: Math.floor(Date.now() / 1000) + 600,
              messages: [{
                  address: treasuryAddr.toString(),
                  amount: toNano('0.5').toString(),
                  payload: base64url
              }]
          });
          console.log('ClaimJetton transaction sent successfully');
      } catch (error) {
          console.error('Error sending ClaimJetton transaction:', error);
          throw error;
      }
    } catch (error) {
      console.error('Error sending ClaimJetton transaction:', error);
    }
  };

  return (
    <AppRoot>
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          backgroundColor: 'var(--tg-theme-secondary-bg-color, #f9f9f9)',
          minHeight: '100vh',
        }}
      >
        <div style={{padding: 16, width: '100%', maxWidth: 530, paddingBottom: 50 }}>
          {/* Назад */}
          <Button
            onClick={() => navigate(-1)}
            size="l"
            style={{
              marginBottom: 16,
              backgroundColor: 'var(--tg-theme-bg-color, #eee)',
              color: 'var(--tg-theme-text-color, #000)',
            }}
          >
            ← Назад
          </Button>

          {/* Картинка */}
          <img
            src={asset.image}
            alt={asset.name}
            style={{
              width: '100%',
              borderRadius: 16,
              objectFit: 'cover',
              marginBottom: 16,
            }}
          />

          {/* Название + подзаголовок */}
          <Title level="2" style={{ marginBottom: 4, color: 'var(--tg-theme-text-color, #000)' }}>
            {asset.name}
          </Title>

          <Text style={{ color: 'var(--tg-theme-hint-color, #666)', marginBottom: 12 }}>
            {asset.subtitle}
          </Text>

          {/* Прогресс */}
          <div
            style={{
              height: 10,
              backgroundColor: 'var(--tg-theme-secondary-bg-color, #eee)',
              borderRadius: 8,
              overflow: 'hidden',
              marginBottom: 8,
            }}
          >
            <div
              style={{
                height: '100%',
                width: `${progress}%`,
                backgroundColor: 'var(--tg-theme-link-color, #3390ec)',
              }}
            />
          </div>

          <Text style={{ fontSize: 14, marginBottom: 16, color: 'var(--tg-theme-text-color, #000)' }}>
            {asset.raised.toLocaleString()} USDT ({progress}%) из {asset.goal.toLocaleString()} USDT
          </Text>

          {asset.raised < asset.goal ? (
            <Button size="l" style={{ width: '100%', marginBottom: 24 }} onClick={() => navigate(`/asset/${id}/buy`)}>
              Купить долю
            </Button>
          ) : (
            <Button size="l" style={{ width: '100%', marginBottom: 24, backgroundColor: '#4CAF50', color: '#fff' }} onClick={handleClaim}>
              Получить выплату
            </Button>
          )}

          {/* Таблица параметров */}
          <ProfileRowContainer style={{ marginBottom: 24, width:'100%', maxWidth:500 }}>
            <ProfileRow
              label="Обещанный доход:"
              value={asset.returnRate ?? '—'}
              hint="Средняя прогнозируемая доходность согласно нашей статистике"
            />
            <ProfileRow
              label="Срок службы:"
              value={asset.lifetime ?? '—'}
              hint="Продаем машину и распределяем сумму продажи по истечении этого срока"
            />
            <ProfileRow label="Стоимость жетона:" value="1 USDT" />
            <ProfileRow
              label="Дата начала продаж:"
              value={asset.saleStartDate ?? '—'}
              last
            />
          </ProfileRowContainer>

          {/* Описание */}
          <Title level="3" style={{ marginBottom: 8, color: 'var(--tg-theme-text-color, #000)' }}>
            О проекте
          </Title>
          <div style={{ marginBottom: 24 }}>
            <Text style={{ color: 'var(--tg-theme-text-color, #000)', whiteSpace: 'pre-line' }}>
              {asset.description}
            </Text>
          </div>

          {/* Владельцы */}
          <div
            onClick={() =>
              window.Telegram?.WebApp?.openLink(`https://testnet.tonviewer.com/${id}?section=holders`)
            }
            style={{
              width: '94%',
              maxWidth: 570,
              marginBottom: 12,
              padding: '12px 16px',
              borderRadius: 8,
              backgroundColor: 'var(--tg-theme-secondary-bg-color, #eaeaea)',
              color: 'var(--tg-theme-text-color, #000)',
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
              cursor: 'pointer',
              fontWeight: 600,
            }}
          >
            <Text style={{ fontSize: 18, fontWeight: 600 }}>Владельцы</Text>
            <Text style={{ fontSize: 20 }}>›</Text>
          </div>

          {/* Документы */}
          <div
            onClick={() => {
              if (promoDoc) {
                window.Telegram?.WebApp?.openLink(promoDoc);
              } else {
                alert('Документ не найден');
              }
            }}
            style={{
              width: '94%',
              maxWidth: 570,
              marginBottom: 12,
              padding: '12px 16px',
              borderRadius: 8,
              backgroundColor: 'var(--tg-theme-secondary-bg-color, #eaeaea)',
              color: 'var(--tg-theme-text-color, #000)',
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
              cursor: 'pointer',
              fontWeight: 600,
            }}
          >
            <Text style={{ fontSize: 18, fontWeight: 600 }}>Документы</Text>
            <Text style={{ fontSize: 20 }}>›</Text>
          </div>

          {/* Финальная кнопка */}
          {asset.raised < asset.goal && (
            <Button size="l" style={{ width: '100%', marginBottom: 32 }} onClick={() => navigate(`/asset/${id}/buy`)}>
              Купить долю
            </Button>
          )}
        </div>
      </div>
    </AppRoot>
  );
};
